import {
  Component,
  Event,
  EventEmitter,
  Host,
  Prop,
  State,
  h,
} from '@stencil/core';
import { createColorClasses } from '../../../utils/functions/color.function';
import type { TpColor } from '../../../utils/types/color.type';
import { TP_BUTTON_CLUSTER } from './utils/button-cluster.constant';

@Component({
  tag: 'tp-button-cluster',
  styleUrl: 'button-cluster.scss',
  assetsDirs: ['assets'],
  shadow: true,
})
export class TpButtonCluster {
  /**
   * Define a variação de cor do componente.
   */
  @Prop({ reflect: true }) color: TpColor;

  /**
   * Define o estato inicial do componente.
   */
  @Prop({ reflect: true }) initialCollapsedState = true;

  /**
   * Define a variação de estado do componente.
   */
  @State() collapsed = true;

  /**
   * Emitido quando a propriedade collapsed é alterada.
   */
  @Event() tpCollapse: EventEmitter<boolean>;

  /**
   * Métodos internos.
   */
  toggle() {
    this.collapsed = !this.collapsed;
    this.tpCollapse.emit(this.collapsed);
  }

  /**
   * Lifecycle hooks.
   */
  componentWillLoad() {
    if (this.initialCollapsedState !== this.collapsed) {
      this.collapsed = this.initialCollapsedState;
    }
  }

  render() {
    const { color, collapsed } = this;

    return (
      <Host
        class={createColorClasses(color, {
          'tp-button-cluster': true,
          'tp-button-cluster--collapsed': collapsed,
        })}
      >
        <div class="tp-button-cluster__container" onClick={() => this.toggle()}>
          <slot name="text-expand"></slot>
          <ion-icon
            class="tp-button-cluster__icon"
            src={TP_BUTTON_CLUSTER.icons.chevronDown}
            aria-hidden="true"
          ></ion-icon>
          <slot name="text-collapse"></slot>
        </div>
      </Host>
    );
  }
}
